* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}
#app {
    display: none;
    background: #fff;
    overflow: hidden;
    &.active {
        display: block;
    }
    .header {
        position: fixed;
        top: 0;
        height: 15vh;
        z-index: 1;
        .topbar {
            height: 10vh;
            background:#060a3d;
            color:#fff;
        }
        nav>ul {
            background:#fff;
            display: flex;
            flex-wrap: wrap;
            font-size: 20px;
            li {
                text-align: center;
                width: calc(100vw/3);
                &active {
                    color:red;
                }
            }
        }
    }

    .main {
        .container{
            margin:15vh 0;
            position: absolute;
            h3 {
                border-left: 3px solid red;
                margin: 5px 0;
            }
            .hotList {
                .box {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    li {
                        width: calc(94vw/3);
                        height: 20vh;
                        .img {
                            height:80%;
                            background-size: cover;
                        }
                        h4 {
                            height:20%;
                        }
                    }
                }
            }
            .dayChoose {
                ul {
                    li {
                        border-top: 1px solid #ccc;
                        text-align: left;
                        overflow: hidden;
                        padding: 5px 0;
                        font-size: 13px;
                        img {
                            float: left;
                            width: 3em;
                            height: 3em;
                        }
                        h5 {
                            font-size: 1.2em;
                            overflow: hidden;
                        }
                        p {
                            font-size: 0.8em;
                            color: #888;
                        }
                        .sq{
                            width: 1em;
                            height: 1em;
                            float: left;
                            padding-top: 2px;
                        }
                        .play {
                            float:right;
                            width: 1.5em;
                            height: 1.5em;
                            margin-top: 0.75em;
                            margin-right: 1em;
        
                        }
        
                    }
                }
            }

      
        }
    }
    
    .footer {
        height: 10vh;
        width: 100%;
        position: fixed;
        bottom: 0;
        background: #060a3d;
        z-index: 1;
        .actions {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 1vh 20px;
            svg {
                width: 28px;
                height: 28px;
                &.btn-play-pause {
                    width: 50px;
                    height: 50px;
                }
            }

        }
    }


    
}